@font-face {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 300;
    src: url('https://fonts.googleapis.com/css?family=Roboto&display=swap');
}

h1{
    font-family: 'Roboto';
    text-align: center;
}

img{
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%;
    height: auto;
    width: auto;
    max-width: 200px;
    max-height: 200px;
}

.nameContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}